.window {
    display: flex;
    flex-direction: column;
    position: absolute;
    z-index: 1002;
    width: 720px;
    height: max-content;
    max-height: 90vh;
    overflow-y: auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 2px solid #4c4cff;
    border-radius: 40px;
    justify-content: start;
    align-items: center;
    background: #1c1c21;

    .title {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 64px;
        padding: 60px 40px;

        button {
            background: none;
            border: none;
            cursor: pointer;
        }

        svg {
            opacity: 0.2;
            transition: all 0.5s ease;
        }
        svg:hover {
            opacity: 1;
        }
    }
}
