.container {
    height: 912px;
    width: 600px;
    overflow: hidden;
    padding-bottom: 40px;

    .update {
        display: flex;
        align-items: center;
        justify-content: center;
        button {
            display: flex;
            margin-top: 100px;
        }
    }
}

.title {
    font-family: 'JetBrains Mono', monospace;
    font-size: 36px;
    font-weight: 600;
    color: #f2f2f3;
    line-height: 48px;
    margin-top: 40px;
}

.tabs {
    display: flex;
    margin-top: 20px;
    height: 70px;
    width: max-content;
    justify-content: space-between;
}

.ingredientsContainer {
    overflow-y: scroll;
    padding-right: 16px;
    margin-top: 40px;
    height: 600px;
}

.ingredientsContainer::-webkit-scrollbar {
    width: 8px;
}

.ingredientsContainer::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 4px;
}

.ingredientsContainer::-webkit-scrollbar-thumb {
    background: linear-gradient(180deg, #4c4cff 0%, #00cccc 100%);
    border-radius: 4px;
}

.ingredientsContainer::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(180deg, #6666ff 0%, #33dddd 100%);
}

.section {
    margin-bottom: 40px;
}

.sectionTitle {
    font-family: 'JetBrains Mono', monospace;
    font-size: 24px;
    font-weight: 600;
    color: #f2f2f3;
    margin: 0 0 24px 0;
    line-height: 30px;
}

.ingredientsList {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 32px 24px;
}

.ingredientCard {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    cursor: pointer;
    background: linear-gradient(
        135deg,
        rgba(28, 28, 33, 0.6) 0%,
        rgba(19, 19, 22, 0.6) 100%
    );
    border: 1px solid rgba(79, 79, 255, 0.2);
    border-radius: 16px;
    transition: all 0.3s ease;
    backdrop-filter: blur(8px);
    width: 272px;
    height: 208px;
    justify-content: center;
    align-items: center;
}

.ingredientCard:hover {
    border-color: rgba(79, 79, 255, 0.6);
    box-shadow: 0 8px 32px rgba(79, 79, 255, 0.2);
    transform: translateY(-2px);
}

.ingredientImage {
    width: 240px;
    height: 120px;
    object-fit: contain;
}

.priceContainer {
    display: flex;
    align-items: center;
    gap: 8px;
    text-align: center;
}

.price {
    font-family: 'JetBrains Mono', monospace;
    font-size: 20px;
    font-weight: 600;
    color: #f2f2f3;
    line-height: 28px;
    text-align: center;
}

.ingredientName {
    font-family: 'JetBrains Mono', monospace;
    font-size: 16px;
    font-weight: 400;
    color: #f2f2f3;
    text-align: center;
    margin: 0;
    line-height: 24px;
    min-height: 48px;
    width: 272px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 4px;
}
