.order {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #f2f2f3;
    margin-bottom: 120px;

    h5 {
        font-weight: 700;
        font-size: 24px;
        line-height: 30px;
        margin: 0;
        padding: 0;
        margin-top: 32px;
    }

    img {
        width: 120px;
        height: 120px;
        margin: 60px 0;
    }

    h6 {
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        margin: 0;
        padding: 0;
    }
}

.wait {
    color: #8585ad;
}
.orderNumber {
    margin: 0;
    padding: 0;
    margin-top: 36px;
    font-weight: 400;
    font-size: 144px;
    line-height: 120px;
    text-shadow: 0 0 30px rgb(59, 59, 247);
}
